<template>
  <div id="ibs-attachment-modal">
    <a-button type="primary" v-show="!isShow" @click="showModal">{{
      t("attachmentUpload.Upload_attachment")
    }}</a-button>
    <a-modal
      width="900px"
      :title="t('attachmentUpload.Upload_file')"
      :visible="visible"
      :destroyOnClose="true"
      @ok="handleOk"
      :getContainer="getContainer"
      @cancel="handleCancel"
    >
      <div class="ibs-uploader-content">
        <div class="ibs-uploader-container" id="uploader-container"></div>
        <div class="ibs-uploader-bottom">
          <ul>
            <li>
              {{ t("attachmentUpload.Supported_formats") }}
              <strong>{{
                uploaderInitData.fileSingleSizeLimit | getSingleFileSize
              }}</strong
              >
            </li>
            <li>
              {{ t("attachmentUpload.Support_breakpoint_renewal") }}
            </li>
          </ul>
        </div>
      </div>
      <template slot="footer">
        <a-button key="submit" type="primary" @click="handleOk">
          {{ t("attachmentUpload.Close") }}
        </a-button>
      </template>
    </a-modal>

    <attachment-preview
      v-show="isShow"
      :isDelete="true"
      :isPreview="isPreview"
      :isDownload="false"
      :bodyDom="bodyDom"
      :fileData="fileFinalData"
      @deleteFileItem="deleteFileItem"
      @previewAttachment="previewAttachment"
      :deleteAttachmentCallback="deleteAttachmentCallback"
      :previewAttachmentCallback="previewAttachmentCallback"
    ></attachment-preview>
  </div>
</template>

<script>
import attachmentPreview from "@/components/attachment-preview/src/attachment.vue";
import loadScript from "load-script";
import Locale from "@/mixins/locale";
const uploaderDefaultInitData = {
  sdkBaseUri: "",
  disableDataUpload: 0,
  disableSentry: 0,
  initUrl:
    "//es-dev.com/uploader/v2/init?token=MnxhdHRhY2htZW50fDJ8cHJpdmF0ZXwxNTg3ODg4Mjc1fDU2YmUzMmJhZjI5OWVjYzA4NDdmNGU5NWMzYWMxZjNj",
  finishUrl:
    "//es-dev.com/uploader/v2/finished?token=MnxhdHRhY2htZW50fDJ8cHJpdmF0ZXwxNTg3ODg4Mjc1fDU2YmUzMmJhZjI5OWVjYzA4NDdmNGU5NWMzYWMxZjNj",
  // B
  fileSingleSizeLimit: 2048 * 1024 * 1024,
  locale: "zh_CN",
  // 
  accept: {
    extensions: [
      "mp4",
      "avi",
      "flv",
      "f4v",
      "mpg",
      "wmv",
      "mov",
      "vob",
      "rmvb",
      "mkv",
      "m4v",
      "mp3",
      "ppt",
      "pptx",
      "doc",
      "docx",
      "pdf",
      "xls",
      "xlsx",
      "wps",
      "odt",
      "zip",
      "rar",
      "gz",
      "tar",
      "7z"
    ],
    mimeTypes: [
      "video/mp4",
      "video/mpeg",
      "video/x-msvideo",
      "video/quicktime",
      "video/3gpp",
      "video/x-m4v",
      "video/x-flv",
      "video/x-ms-wmv",
      "audio/mp4",
      "audio/mpeg",
      "audio/basic",
      "audio/ac3",
      "audio/ogg",
      "audio/3gpp",
      "application/vnd.ms-powerpoint",
      "application/vnd.openxmlformats-officedocument.presentationml.presentation",
      "application/vnd.ms-excel",
      "application/vnd.ms-outlook",
      "application/vnd.ms-pkicertstore",
      "application/vnd.ms-pkiseccat",
      "application/vnd.ms-pkistl",
      "application/vnd.ms-powerpoint",
      "application/vnd.ms-project",
      "application/vnd.ms-works",
      "application/msword",
      "application/pdf",
      "application/vnd.openxmlformats-officedocument.wordprocessingml.document",
      "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      "application/zip",
      "application/x-zip-compressed",
      "application/x-rar-compressed",
      "application/x-tar",
      "application/x-gzip",
      "application/x-7zip"
    ]
  },
  // 
  id: "uploader-container",
  process: { document: { type: "html" } },
  ui: "single"
};
const attachmentFileDefault = {
  type: "other",
  filename: ".pptx",
  id: "500"
};
export default {
  name: "attachment-upload",
  mixins: [Locale],
  data() {
    return {
      visible: false,
      fileTypeObj: {
        ppt: "ppt",
        pptx: "ppt",
        doc: "document",
        docx: "document",
        pdf: "document",
        xls: "document",
        xlsx: "document",
        mp4: "video",
        avi: "video",
        flv: "video",
        f4v: "video",
        mpg: "video",
        wmv: "video",
        mov: "video",
        vob: "video",
        rmvb: "video",
        mkv: "video",
        m4v: "video",
        mp3: "audio"
      },
      fileData: {},
      isShow: false,
      isPreview: false
    };
  },
  components: {
    attachmentPreview
  },
  props: {
    cdnHost: {
      type: String,
      default: "service-cdn.qiqiuyun.net"
    },
    uploaderData: {
      type: Object,
      default() {
        return {};
      }
    },
    bodyDom: {
      type: String,
      default() {
        return "ibs-attachment-modal";
      }
    },
    fileShowData: {
      type: Object,
      default() {
        return {};
      }
    },
    attachmentFile: {
      type: Object,
      default() {
        return attachmentFileDefault;
      }
    },
    mode: {
      type: String,
      default: "create"
    },
    module: {
      type: String,
      default: ""
    },
    deleteAttachmentCallback: {
      type: Function,
      default() {
        return new Promise(resolve => {
          resolve();
        });
      }
    },
    previewAttachmentCallback: {
      type: Function,
      default() {
        return new Promise(resolve => {
          resolve();
        });
      }
    }
  },
  computed: {
    uploaderSDK() {
      return `//${this.cdnHost}/js-sdk/uploader/sdk-2.1.0.js?${this.timestamp}`;
    },
    uploaderInitData() {
      if (Object.keys(this.uploaderData).length) {
        return Object.assign(uploaderDefaultInitData, this.uploaderData);
      }
      return uploaderDefaultInitData;
    },
    timestamp() {
      return Math.round(Date.parse(new Date()) / 10000);
    },
    fileFinalData() {
      if (this.mode == "edit" && this.fileShowData.id) {
        return this.fileShowData;
      } else {
        return this.fileData;
      }
    }
  },
  filters: {
    getSingleFileSize(fileSize) {
      return fileSize >= 1024 * 1024 * 1024
        ? `${fileSize / (1024 * 1024 * 1024)}GB`
        : `${fileSize / (1024 * 1024)}MB`;
    }
  },
  mounted() {
    if (this.mode == "edit" && this.fileShowData.id) {
      this.isShow = true;
      this.isPreview = true;
    }
  },
  methods: {
    showModal() {
      this.visible = true;
      this.isPreview = false;
      loadScript(this.uploaderSDK, err => {
        console.log(err);
        this.initUpladerSDk();
      });
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    },
    handleCancel(e) {
      console.log(e);
      this.visible = false;
    },
    initUpladerSDk() {
      let uploader = new window.UploaderSDK(this.uploaderInitData);
      uploader.on("error", type => {
        // 
        this.$message.error(type.message);
      });
      uploader.on("file.finish", file => {
        console.log("finish");
        // 
        this.visible = false;
        this.isShow = true;
        // 
        this.fileData = {
          id: file.id,
          file_name: file.name,
          file_type: this.getFileType(file)
        };
        file.module = this.module;
        file.file_type = this.getFileType(file);
        file.file_name = file.name;
        const index = file.name.lastIndexOf(".");
        file.ext = file.name.substr(index + 1).toLowerCase();
        this.$emit("getFileInfo", file);
      });
    },
    getFileType(file) {
      const index = file.name.lastIndexOf(".");
      const extension = file.name.substr(index + 1).toLowerCase();
      return this.fileTypeObj[extension]
        ? this.fileTypeObj[extension]
        : "other";
    },
    deleteFileItem(flag) {
      this.isShow = flag;
      // 
      this.$emit("deleteFile", this.fileFinalData.id, flag);
    },
    getContainer() {
      console.log(this.bodyDom);
      return document.getElementById(this.bodyDom);
    },
    previewAttachment(fileId) {
      this.$emit("previewFile", fileId);
    }
  }
};
</script>
