<template>
  <div id="ibs-attachment-preview">
    <div class="ibs-attachment-item ibs-clearfix">
      <img
        class="ibs-mr8 ibs-left"
        :src="coverImgSrc"
        height="64px"
        width="112px"
      />
      <p class="ibs-attachment-item__name ibs-text-overflow ibs-left">
        {{ fileData.file_name }}
      </p>
      <div class="ibs-right ibs-attachment-item__btn ibs-dark-minor ibs-right">
        <a-tooltip
          placement="top"
          v-if="isPreview"
          :getPopupContainer="getPopupContainer"
        >
          <template slot="title" v-if="disabled">
            <span v-if="fileData.file_type == 'other' || isExcel">{{
              t("attachmentPreview.File_not_supported")
            }}</span>
            <span v-else>{{ t("attachmentPreview.Transcoding") }}</span>
          </template>
          <a-button
            type="primary"
            ghost
            size="small"
            :disabled="disabled"
            @click="showModal"
            >{{ t("attachmentPreview.Preview") }}</a-button
          >
        </a-tooltip>

        <a-button
          class="ibs-ml16"
          type="primary"
          size="small"
          :href="downloadUrl"
          @click="downloadAttachment"
          :disabled="disabledDownload"
          v-if="isDownload"
          :loading="downloadLoading"
          >{{ t("attachmentPreview.Download") }}</a-button
        >
        <a-button
          v-if="isDelete"
          type="link"
          :loading="loading"
          @click="deleteAttachment"
          >{{ t("attachmentPreview.Delete") }}</a-button
        >
      </div>
    </div>
    <a-modal
      width="900px"
      class="ibs-attachment-preview-modal"
      :title="t('attachmentPreview.Preview')"
      :visible="visible"
      :destroyOnClose="true"
      @ok="handleOk"
      :getContainer="getContainer"
      @cancel="handleCancel"
    >
      <div
        v-if="fileData.file_type === 'audio'"
        id="global-player"
        style="width: 100%;height: 100%;height: 400px"
        class="video-js vjs-default-skin"
        controls
        preload="auto"
      ></div>
      <div v-else id="global-player" style="height: 400px"></div>

      <template slot="footer">
        <a-button key="submit" type="primary" @click="handleOk">
          {{ t("attachmentPreview.Close") }}
        </a-button>
      </template>
    </a-modal>
  </div>
</template>

<script>
import loadScript from "load-script";
import coverImgData from "./img.json";
import Locale from "@/mixins/locale";
const defaultPlayerInitData = {
  id: "global-player",
  playServer: "",
  sdkBaseUri: "",
  disableDataUpload: 0,
  disableSentry: 0,
  resNo: "e1daaab4f2254162a07c0e9461f89684",
  token: "7h3uCe6qa4t1CxzV:1587367870:ie6JBYIkvGvtHWq1lpte11Abrv0=",
  user: { id: 2, name: "test@edusoho.com" }
};
const defaultAudioInitData = {
  id: "global-player",
  statsInfo: {
    accesskey: "6uVG1xmibb3EX7XhUV3g6jflPidNhNon",
    globalId: "7483108e0b7f48349726b305180aea3b",
    userId: 2,
    userName: "test@edusoho.com"
  },
  playlist: "",
  autoplay: true,
  customPos: "0",
  remeberLastPos: false,
  sequentialMode: true,
  language: "zh-CN"
};

const defaultFileData = {
  id: "10",
  file_name: ".mp3",
  file_type: "audio",
  ext: "mp3",
  convert_status: "success"
};

export default {
  name: "attachment-preview",
  mixins: [Locale],
  data() {
    return {
      visible: false,
      fileTypeObj: {
        ppt: "ppt",
        pptx: "ppt",
        doc: "document",
        docx: "document",
        pdf: "document",
        xls: "document",
        xlsx: "document",
        mp4: "video",
        avi: "video",
        flv: "video",
        f4v: "video",
        mpg: "video",
        wmv: "video",
        mov: "video",
        vob: "video",
        rmvb: "video",
        mkv: "video",
        m4v: "video",
        mp3: "audio"
      },
      loading: false,
      downloadLoading: false,
      downloadUrl: "javascript:;",
      disabledDownload: false
    };
  },
  props: {
    cdnHost: {
      type: String,
      default: "service-cdn.qiqiuyun.net"
    },
    fileData: {
      type: Object,
      default() {
        return defaultFileData;
      }
    },
    bodyDom: {
      type: String,
      default() {
        return "ibs-attachment-preview";
      }
    },
    isPreview: {
      type: Boolean,
      default() {
        return true;
      }
    },
    isDelete: {
      type: Boolean,
      default() {
        return false;
      }
    },
    isDownload: {
      type: Boolean,
      default() {
        return true;
      }
    },
    deleteAttachmentCallback: {
      type: Function,
      default() {
        return new Promise(resolve => {
          resolve();
        });
      }
    },
    previewAttachmentCallback: {
      type: Function,
      default() {
        return new Promise(resolve => {
          resolve();
        });
      }
    },
    downloadAttachmentCallback: {
      type: Function,
      default() {
        return new Promise(resolve => {
          resolve();
        });
      }
    }
  },
  computed: {
    coverImgSrc() {
      return coverImgData[this.fileData.file_type];
    },
    playerSDK() {
      return this.fileData.file_type === "audio"
        ? `//${this.cdnHost}/js-sdk/audio-player/sdk-v1.js?${this.timestamp}`
        : `//${this.cdnHost}/js-sdk/sdk-v1.js?${this.timestamp}`;
    },
    timestamp() {
      return Math.round(Date.parse(new Date()) / 10000);
    },
    isExcel() {
      return this.fileData.ext === "xlsx" || this.fileData.ext === "xls";
    },
    disabled() {
      return (
        this.fileData.file_type === "other" ||
        this.fileData.convert_status !== "success" ||
        this.isExcel
      );
    }
  },
  methods: {
    showModal() {
      this.visible = true;
      this.$emit("previewAttachment", this.fileData.id);

      this.initSDK();
    },
    initSDK() {
      this.previewAttachmentCallback(3000).then(value => {
        if (value.result) {
          loadScript(this.playerSDK, err => {
            console.log(err);
            if (value.data.type === "audio") {
              new window.AudioPlayerSDK(
                Object.assign(defaultAudioInitData, value.data)
              );
            } else {
              new window.QiQiuYun.Player(
                Object.assign(defaultPlayerInitData, value.data)
              );
            }
          });
        } else {
          this.$message.error(value.msg);
        }
      });
    },
    handleOk(e) {
      console.log(e);
      this.visible = false;
    },
    handleCancel(e) {
      console.log(e);
      this.visible = false;
    },
    deleteAttachment() {
      this.loading = true;
      this.$emit("deleteFileItem", true);
      this.deleteAttachmentCallback(3000).then(value => {
        if (value.result) {
          this.$emit("deleteFileItem", false);
          this.loading = false;
        } else {
          this.$message.error(value.msg);
          this.loading = false;
        }
      });
    },
    getContainer() {
      console.log(this.bodyDom);
      return document.getElementById(this.bodyDom);
    },
    downloadAttachment() {
      if (this.downloadUrl == "javascript:;") {
        this.downloadLoading = true;
        this.$emit("downloadAttachment", this.fileData.id);
        this.downloadAttachmentCallback(3000).then(value => {
          if (value.result) {
            this.downloadUrl = value.url;
            window.location.href = this.downloadUrl;
            this.downloadLoading = false;
          } else {
            this.$message.error(value.msg);
            this.downloadLoading = false;
          }
        });
      }
    },
    getPopupContainer() {
      return document.getElementById("ibs-attachment-preview");
    }
  }
};
</script>
