<template>
  <base-type
    @getFromInfo="getFromInfo"
    :form="form"
    v-bind="$attrs"
    :mode="mode"
  >
    <template v-slot:questions>
      <slot name="stem">
        <a-form-item
          :label="t('Stem')"
          :label-col="{ span: 4 }"
          :wrapper-col="{ span: 16 }"
          required
          :validate-status="stem.validateStatus"
          :help="stem.errorMsg"
        >
          <a-textarea
            :data-image-download-url="
              showCKEditorData.filebrowserImageDownloadUrl
            "
            :rows="0"
            v-decorator="[
              `questions['stem']`,
              {
                initialValue: questions.stem,
                rules: [{ required: true }]
              }
            ]"
          />
        </a-form-item>
      </slot>
      <a-form-item
        v-if="showAttachment"
        :label="t('itemManage.StemAttachment')"
        :label-col="{ span: 4 }"
        :wrapper-col="{ span: 16 }"
      >
        <attachment-upload
          bodyDom="item-bank-create"
          :mode="mode"
          module="stem"
          :fileShowData="getAttachmentTypeData('stem')"
          :cdnHost="cdnHost"
          :uploaderData="uploadSDKInitData"
          :deleteAttachmentCallback="deleteAttachmentCallback"
          :previewAttachmentCallback="previewAttachmentCallback"
          @getFileInfo="getFileInfo"
          @deleteFile="deleteFile"
          @previewFile="previewFile"
        ></attachment-upload>
      </a-form-item>

      <slot name="response_points"></slot>

      <a-collapse :bordered="false">
        <template v-slot:expandIcon="props">
          <div :rotate="props.isActive ? 90 : 0"></div>
        </template>
        <a-collapse-panel
          :header="t('itemManage.ToggleShowChoose')"
          key="1"
          :style="customStyle"
          :forceRender="true"
        >
          <a-form-item
            :label="t('Explain')"
            :label-col="{ span: 4 }"
            :wrapper-col="{ span: 16 }"
          >
            <a-textarea
              :rows="4"
              :data-image-download-url="
                showCKEditorData.filebrowserImageDownloadUrl
              "
              v-decorator="[
                `questions['analysis']`,
                { initialValue: questions.analysis }
              ]"
            />
          </a-form-item>

          <a-form-item
            v-if="showAttachment"
            :label="t('ExplainAttachment')"
            :label-col="{ span: 4 }"
            :wrapper-col="{ span: 16 }"
          >
            <attachment-upload
              bodyDom="item-bank-create"
              :mode="mode"
              module="analysis"
              :cdnHost="cdnHost"
              :uploaderData="uploadSDKInitData"
              :deleteAttachmentCallback="deleteAttachmentCallback"
              :previewAttachmentCallback="previewAttachmentCallback"
              :fileShowData="getAttachmentTypeData('analysis')"
              @getFileInfo="getFileInfo"
              @deleteFile="deleteFile"
              @previewFile="previewFile"
            ></attachment-upload>
          </a-form-item>

          <a-form-item
            :label="t('Score')"
            :label-col="{ span: 4 }"
            :wrapper-col="{ span: 16 }"
          >
            <a-input
              style="width: 200px"
              type="number"
              v-decorator="[
                `questions['score']`,
                {
                  rules: [{ validator: checkScore }],
                  initialValue: questions.score
                }
              ]"
            />
          </a-form-item>
        </a-collapse-panel>
      </a-collapse>
    </template>
  </base-type>
</template>
<script>
import baseType from "./base";
import attachmentUpload from "@/components/attachment-upload/src/upload.vue";
import loadScript from "load-script";
import Emitter from "@/mixins/emitter";
import Locale from "@/mixins/locale";
const base = {
  stem: "",
  seq: 5,
  score: "2.0",
  answer: [],
  analysis: "",
  attachments: []
};
export default {
  name: "answer-model",
  components: { baseType, attachmentUpload },
  mixins: [Emitter, Locale],
  data() {
    return {
      stem: {
        validateStatus: "",
        errorMsg: ""
      },
      customStyle: "background: #ffffff;border: 0",
      publicPath: process.env.BASE_URL,
      stemEditor: "",
      analysisEditor: "",
      inheritAttrs: false
    };
  },
  props: {
    questions: {
      type: Object,
      default: () => base
    },
    form: {
      type: Object,
      default: () => {}
    },
    itemType: {
      type: String,
      default: ""
    },
    mode: {
      type: String,
      default: "create"
    }
  },
  inject: [
    "showCKEditorData",
    "showAttachment",
    "uploadSDKInitData",
    "cdnHost",
    "deleteAttachmentCallback",
    "previewAttachmentCallback"
  ],
  created() {},
  mounted() {
    this.$nextTick(() => {
      loadScript(this.showCKEditorData.jqueryPath, err => {
        if (err) {
          console.log(err);
        }
        loadScript(this.showCKEditorData.publicPath, err => {
          if (err) {
            console.log(err);
          }
          // 
          if (this.itemType !== "fill") {
            this.initBaseStem();
          }
          this.initBaseAnalysis();
        });
      });
    });
  },
  methods: {
    initBaseStem() {
      this.stemEditor = window.CKEDITOR.replace(
        "base-question-type_questions['stem']",
        {
          toolbar: "Minimal",
          fileSingleSizeLimit: this.showCKEditorData.fileSingleSizeLimit,
          filebrowserImageUploadUrl: this.showCKEditorData
            .filebrowserImageUploadUrl,
          filebrowserImageDownloadUrl: this.showCKEditorData
            .filebrowserImageDownloadUrl,
          language: this.showCKEditorData.language
        }
      );
      this.stemEditor.setData(this.questions.stem);
      this.stemEditor.on("change", () => {
        const data = this.stemEditor.getData();
        this.form.setFieldsValue({ [`questions['stem']`]: data });
        this.checkStem(data);
      });
      this.stemEditor.on("blur", () => {
        const data = this.stemEditor.getData();
        this.form.setFieldsValue({ [`questions['stem']`]: data });
        this.checkStem(data);
      });
    },
    initBaseAnalysis() {
      this.analysisEditor = window.CKEDITOR.replace(
        "base-question-type_questions['analysis']",
        {
          toolbar: "Minimal",
          fileSingleSizeLimit: this.showCKEditorData.fileSingleSizeLimit,
          filebrowserImageUploadUrl: this.showCKEditorData
            .filebrowserImageUploadUrl,
          filebrowserImageDownloadUrl: this.showCKEditorData
            .filebrowserImageDownloadUrl,
          language: this.showCKEditorData.language
        }
      );
      this.analysisEditor.setData(this.questions.analysis);
      this.analysisEditor.on("change", () => {
        this.form.setFieldsValue({
          [`questions['analysis']`]: this.analysisEditor.getData()
        });
        this.outanalysis = this.analysisEditor.getData();
      });
      this.analysisEditor.on("blur", () => {
        this.form.setFieldsValue({
          [`questions['analysis']`]: this.analysisEditor.getData()
        });
        this.outanalysis = this.analysisEditor.getData();
      });
    },
    checkScore(rule, value, callback) {
      const fractionRule = /^(([1-9]{1}\d{0,2})|([0]{1}))(\.(\d){1})?$/;
      if (value < 0) {
        callback(this.t("ScoreRule.one"));
      } else if (value > 999) {
        callback(this.t("ScoreRule.two"));
      } else if (!fractionRule.test(value)) {
        callback(this.t("ScoreRule.three"));
      } else {
        callback();
      }
    },
    checkStem(value) {
      if (!value) {
        this.stem.validateStatus = "error";
        this.stem.errorMsg = this.t("itemManage.StemRule");
      } else {
        this.stem.validateStatus = "";
        this.stem.errorMsg = "";
      }
    },
    getFromInfo() {
      this.checkStem(this.form.getFieldValue(`questions['stem']`));
      this.$emit("getFromInfo");
    },
    getFileInfo(file) {
      this.questions.attachments.push(file);
      this.$emit("getUploadFile", file);
    },
    deleteFile(fileId, flag) {
      if (!flag) {
        this.questions.attachments = this.questions.attachments.filter(item => {
          return item.id !== fileId;
        });
      }
      const data = {
        fileId: fileId,
        flag: flag
      };
      this.dispatch("item-manage", "getDeleteFile", data);
    },
    getAttachmentTypeData(type) {
      if (this.mode === "create") {
        return;
      }
      const [item] = this.questions.attachments.filter(item => {
        return item.module == type;
      });
      return item;
    },
    previewFile(fileId) {
      this.dispatch("item-manage", "previewFile", fileId);
    }
  }
};
</script>
