<template>
  <div>
    <a-form :form="reviewForm">
      <item-bank
        mode="review"
        :answerReport="answerReport"
        :assessment="assessment"
        :form="reviewForm"
        :role="role"
        :answerScene="answerScene"
        :answerRecord="answerRecord"
        :showCKEditorData="showCKEditorData"
        @previewAttachment="previewAttachment"
        :previewAttachmentCallback="previewAttachmentCallback"
        :downloadAttachmentCallback="downloadAttachmentCallback"
        @downloadAttachment="downloadAttachment"
        :cdnHost="cdnHost"
      >
      </item-bank>
    </a-form>
    <a-modal
      id="ibs-review-modal"
      :title="t('itemReview.FinishReview')"
      :destroyOnClose="true"
      :visible="visible"
      :footer="null"
      @cancel="handleCancel"
      :getContainer="getContainer"
    >
      <div
        class="ibs-review-body ibs-mb16 ibs-clearfix"
        v-show="Number(answerScene.need_score)"
      >
        <div class="ibs-review-body__info ibs-left">
          <div class="ibs-text-xxlg">{{ t("itemReview.Student_score") }}</div>
          <div class="ibs-danger-color ibs-text-xlg">
            {{ commentScore + Number(answerReport.score) }}
          </div>
        </div>
        <div class="ibs-review-body__info ibs-right">
          <div class="ibs-text-lg ibs-dark-assist">
            {{ t("itemReview.Total_score") }}{{ answerReport.total_score }}
          </div>
          <div class="ibs-text-lg ibs-mt8 ibs-dark-assist">
            {{ t("itemReview.Pass_score") }}{{ answerScene.pass_score }}
          </div>
        </div>
      </div>
      <a-form class="ibs-clearfix" :form="form">
        <a-form-item>
          <a-textarea
            :placeholder="t('itemReview.InputComment_tip')"
            v-decorator="[`comment`, { initialValue: '' }]"
          ></a-textarea>
        </a-form-item>
        <a-form-item>
          <a-select
            :defaultValue="comments[0]"
            @change="handleChange"
            :getPopupContainer="getPopupContainer"
          >
            <a-select-option
              v-for="(comment, index) in comments"
              :key="index"
              :value="comment"
              >{{ comment }}</a-select-option
            >
          </a-select>
        </a-form-item>
        <a-form-item v-show="!Number(answerScene.need_score)">
          <a-radio-group
            class="ibs-right"
            v-decorator="[
              'grade',
              {
                initialValue: gradeDefault
              }
            ]"
          >
            <a-radio
              v-for="(grade, index) in grades"
              :key="index"
              :value="grade"
              >{{ gradeObj[grade] }}</a-radio
            >
          </a-radio-group>
        </a-form-item>
        <a-form-item class="ibs-right ibs-review-footer">
          <a-button class="ibs-mr8" key="back" @click="handleCancel">{{
            t("itemReview.Cancel")
          }}</a-button>
          <a-button
            type="primary"
            class="ibs-btn-info ibs-mr8"
            @click="handleFormSubmit"
            >{{ t("itemReview.FinishReview") }}
          </a-button>
          <a-button type="primary" @click="handleFormSubmitAgain">{{
            t("itemReview.FinishThenGo")
          }}</a-button>
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>
<script>
import Locale from "@/mixins/locale";
import itemBank from "@/components/item-engine/src/item.vue";
export default {
  name: "item-review",
  components: {
    itemBank
  },
  mixins: [Locale],
  data() {
    return {
      formLayout: "horizontal",
      reviewForm: this.$form.createForm(this, { name: "essay-comment-form" }),
      commentScore: 0,
      form: this.$form.createForm(this, { name: "comments-form" }),
      visible: false,
      comments: [
        this.t("itemReview.comments_0"),
        this.t("itemReview.comments_1"),
        this.t("itemReview.comments_2"),
        this.t("itemReview.comments_3"),
        this.t("itemReview.comments_4"),
        this.t("itemReview.comments_5")
      ],
      comment: "",
      grades: ["excellent", "good", "passed", "unpassed"],
      gradeObj: {
        excellent: this.t("itemReview.rank_0"),
        good: this.t("itemReview.rank_1"),
        passed: this.t("itemReview.rank_2"),
        unpassed: this.t("itemReview.rank_3")
      },
      commentData: {},
      data: {}
    };
  },
  computed: {
    gradeDefault() {
      return Number(this.answerScene.need_score) ? "" : "passed";
    }
  },
  props: {
    answerReport: {
      type: Object,
      default() {
        return {};
      }
    },
    showCKEditorData: {
      type: Object,
      default() {
        return {};
      }
    },
    assessment: {
      type: Object,
      default() {
        return {};
      }
    },
    answerScene: {
      type: Object,
      default() {
        return {};
      }
    },
    answerRecord: {
      type: Object,
      default() {
        return {};
      }
    },
    cdnHost: {
      type: String,
      default: "service-cdn.qiqiuyun.net"
    },
    previewAttachmentCallback: {
      type: Function,
      default() {
        return new Promise(resolve => {
          resolve();
        });
      }
    },
    downloadAttachmentCallback: {
      type: Function,
      default() {
        return new Promise(resolve => {
          resolve();
        });
      }
    },
    // teacher: student:
    role: {
      type: String,
      default: ""
    }
  },
  created() {
    this.$on("handleSubmit", this.handleSubmit);
  },
  methods: {
    handleSubmit() {
      this.reviewForm.validateFields((err, values) => {
        if (!err) {
          let idList = [];
          for (let i in values) {
            if (i.indexOf("score") > -1) {
              idList.push(i.slice(5));
            }
          }
          let reviewList = [];
          if (this.commentScore) {
            this.commentScore = 0;
          }
          for (let i in idList) {
            let tempObj = {};
            tempObj.id = idList[i];
            tempObj.score = Number(values[`score${idList[i]}`]);
            tempObj.status = values[`status${idList[i]}`];
            this.commentScore += Number(values[`score${idList[i]}`]);
            tempObj.comment = values[`comment${idList[i]}`];
            reviewList.push(tempObj);
          }
          this.data.report_id = this.answerReport.id;
          this.data.question_reports = reviewList;
          this.handleValidateSuccess();
        }
        console.log(err);
      });
    },
    handleValidateSuccess() {
      if (this.role === "student") {
        this.$emit("getReviewData", this.data);
        return;
      }
      this.visible = true;
    },
    handleCancel() {
      this.visible = false;
    },
    handleChange(value) {
      this.comment = value;
      this.form.setFieldsValue({ [`comment`]: this.comment });
    },
    handleFormSubmit(e) {
      this.getFormData(e);
      this.$emit("getReviewData", this.data);
    },
    handleFormSubmitAgain(e) {
      this.getFormData(e);
      this.$emit("getReviewDataAagin", this.data);
    },
    getFormData(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        this.commentData = values;
      });
      this.visible = false;
      this.data = Object.assign(this.data, this.commentData);
    },
    getContainer() {
      return document.getElementById("item-bank-sdk-message");
    },
    getPopupContainer() {
      return document.getElementById("ibs-review-modal");
    },
    previewAttachment(fileId) {
      this.$emit("previewAttachment", fileId);
    },
    downloadAttachment(fileId) {
      this.$emit("downloadAttachment", fileId);
    }
  }
};
</script>
