<template>
  <!--  -->
  <div :class="className" v-if="isShow">
    {{ t("itemEngine.countDown") }}{{ time }}
    <div class="ibs-right ibs-assessment-button" @click="stopTimer">
      <i class="ib-icon ib-icon-pause"></i>
    </div>
  </div>
</template>

<script>
import Emitter from "@/mixins/emitter";
import Locale from "@/mixins/locale";
import { getCountDown } from "@/utils/date-toolkit.js";
export default {
  name: "count-down",
  mixins: [Emitter, Locale],
  data() {
    return {
      time: null,
      timeMeter: null, //
      remainTime: 0
    };
  },
  props: {
    limitedTime: {
      type: Number,
      default: 0
    },
    usedTime: {
      type: Number,
      default: 0
    },
    assessmentStatus: {
      type: String,
      default: ""
    },
    mode: {
      type: String,
      default: "do"
    },
    beginTime: {
      type: Number,
      default: 0
    }
  },
  mounted() {
    this.$nextTick(() => {
      if (this.assessmentStatus === "preview" || this.mode !== "do") {
        return;
      }
      this.timer();
    });
  },
  computed: {
    className: function() {
      return this.limitedTime &&
        this.assessmentStatus !== "preview" &&
        this.mode === "do"
        ? "ibs-assessment-timer ibs-assessment-timer--top"
        : "ibs-assessment-timer";
    },
    isShow: function() {
      return (
        this.beginTime * 1000 +
          this.limitedTime * 60 * 1000 -
          Date.parse(new Date()) >
        0
      );
    }
  },
  methods: {
    getContainer() {
      return document.getElementById("item-bank-sdk-card");
    },
    stopTimer() {
      this.clearTime();
      const self = this;
      this.$warning({
        title: this.t("itemEngine.pause"),
        content: this.t("itemEngine.pause_tip"),
        okText: this.t("itemEngine.goThenExam"),
        getContainer: this.getContainer,
        onOk() {
          self.timer();
        }
      });
    },
    reachTimerSubmit() {
      this.$emit("reachTimeSubmitAnswerData");
    },
    timer() {
      let i = 0;
      const now = Date.parse(new Date());
      let time = this.beginTime * 1000 + this.limitedTime * 60 * 1000 - now;
      console.log(time);
      if (time <= 0) {
        this.reachTimerSubmit();
        return;
      }
      // 
      if (this.remainTime) {
        time = this.remainTime;
      }
      this.timeMeter = setInterval(() => {
        let { hours, minutes, seconds } = getCountDown(time, i++);
        this.time = `${hours}:${minutes}:${seconds}`;
        this.remainTime = time - i * 1000;
        if (
          (Number(hours) == 0 &&
            Number(minutes) == 0 &&
            Number(seconds) == 0) ||
          Number(seconds) < 0
        ) {
          this.clearTime();
          //
          this.reachTimerSubmit();
        }
      }, 1000);
    },
    clearTime() {
      clearInterval(this.timeMeter);
      this.timeMeter = null;
    }
  }
};
</script>
