<template>
  <div class="ibs-engine">
    <div class="ibs-engine-item">
      <a-row>
        <!--  -->
        <a-col :sm="2" :xs="3" class="ibs-text-center" v-show="showScoreAndSeq">
          <div class="ibs-order">{{ question.seq }}</div>
          <span
            class="ibs-score-tag"
            v-show="needScore && Number(!question.isDelete)"
          >
            {{ question.score }}{{ t("itemEngine.score") }}
          </span>
          <a-button
            v-show="showTag"
            class="ibs-mt8 ibs-toggle-btn"
            size="small"
            type="primary"
            :ghost="this.isTag ? false : true"
            @click="changeTag"
            ><i :class="tagClass"></i>{{ tagText }}</a-button
          >
          <a-button
            v-show="showCollect"
            class="ibs-mt8 ibs-collect-btn"
            size="small"
            type="primary"
            :ghost="this.isCollect ? false : true"
            @click="changeCollect"
            ><i :class="collectClass"></i>{{ collectText }}</a-button
          >
        </a-col>
        <a-col
          :xs="showScoreAndSeq ? 21 : 24"
          :sm="showScoreAndSeq ? 22 : 24"
          v-if="Number(!question.isDelete)"
        >
          <!-- word -->
          <div
            class="ibs-stem ibs-danger-color"
            v-if="question.errors && question.errors.stem"
          >
            {{ t("itemEngine.stemErrorMessage") }}
          </div>

          <div
            v-else
            class="ibs-stem ibs-editor-text"
            v-html="question.stem"
          ></div>

          <attachment-preview
            :cdnHost="cdnHost"
            v-if="getAttachmentTypeData('stem')"
            :fileData="getAttachmentTypeData('stem')"
            @previewAttachment="previewAttachment"
            :previewAttachmentCallback="previewAttachmentCallback"
            @downloadAttachment="downloadAttachment"
            :downloadAttachmentCallback="downloadAttachmentCallback"
          ></attachment-preview>
          <slot name="response_points"></slot>
          <div class="ibs-clearfix" v-if="showCollectBtn">
            <a-button
              class="ibs-right ibs-collect-toggle-btn"
              type="primary"
              ghost
              @click="lookAnalysis"
            >
              {{
                canShowAnalysis
                  ? t("itemEngine.closeExplain")
                  : t("itemEngine.openExplain")
              }}
            </a-button>
          </div>
          <div
            v-show="showAnalysis"
            class="ibs-explain ibs-mt16"
            v-html="
              `<span class='ibs-label'>${t(
                'itemEngine.Explain'
              )}</span><div class='ibs-content ibs-editor-text ibs-mr8'>${question.analysis ||
                t('itemReport.no_analysis')}</div>`
            "
          ></div>
          <div
            class="ibs-explain-attachment"
            v-if="
              getAttachmentTypeData('analysis') &&
                mode !== 'do' &&
                mode !== 'review' &&
                question.analysis
            "
          >
            <attachment-preview
              :cdnHost="cdnHost"
              @previewAttachment="previewAttachment"
              @downloadAttachment="downloadAttachment"
              :previewAttachmentCallback="previewAttachmentCallback"
              :downloadAttachmentCallback="downloadAttachmentCallback"
              :fileData="getAttachmentTypeData('analysis')"
            ></attachment-preview>
          </div>
        </a-col>
        <a-col
          :xs="showScoreAndSeq ? 21 : 24"
          :sm="showScoreAndSeq ? 22 : 24"
          class="ibs-text-center"
          v-else
          >{{ deleteTip }}</a-col
        >
      </a-row>
    </div>
  </div>
</template>

<script>
import attachmentPreview from "@/components/attachment-preview/src/attachment.vue";
import Emitter from "@/mixins/emitter";
import Locale from "@/mixins/locale";
export default {
  name: "answer-mode",
  inheritAttrs: false,
  data() {
    return {
      isTag: false,
      isCollect: false,
      canShowAnalysis: false
    };
  },
  components: {
    attachmentPreview
  },
  mixins: [Emitter, Locale],
  computed: {
    tagText() {
      return this.isTag ? this.t("cancel") : this.t("itemEngine.remark");
    },
    tagClass() {
      return this.isTag
        ? "ib-icon ib-icon-bookmark"
        : "ib-icon ib-icon-bookmark-outline";
    },
    collectText() {
      return this.isCollect ? this.t("cancel") : this.t("itemEngine.collect");
    },
    collectClass() {
      return this.isCollect
        ? "ib-icon ib-icon-favorite"
        : "ib-icon ib-icon-favoriteoutline";
    },
    deleteTip() {
      console.log(this.itemType);
      return this.itemType === "material"
        ? this.t("itemEngine.questionIsDeleted")
        : this.t("itemEngine.itemIsDeleted");
    },

    showAnalysis() {
      if (this.doingLookAnalysis) {
        return this.canShowAnalysis;
      } else {
        return this.mode !== "do" && this.mode !== "review";
      }
    },
    showCollectBtn() {
      return this.doingLookAnalysis;
    },
    showCollect() {
      if (!this.Collect) {
        return false;
      }
      return (
        this.mode == "report" &&
        this.recordStatus == "finished" &&
        this.assessmentStatus !== "finished" &&
        Number(!this.question.isDelete)
      );
    },
    showTag() {
      return (
        this.mode == "do" &&
        this.assessmentStatus !== "preview" &&
        Number(!this.question.isDelete)
      );
    }
  },
  inject: [
    "cdnHost",
    "previewAttachmentCallback",
    "downloadAttachmentCallback"
  ],
  props: {
    question: {
      type: Object,
      default() {
        return {};
      }
    },
    needScore: {
      type: Number,
      default() {
        return 1;
      }
    },
    assessmentStatus: {
      type: String,
      default() {
        return "";
      }
    },
    recordStatus: {
      type: String,
      default() {
        return "";
      }
    },
    //  preview: report: do:
    mode: {
      type: String,
      default: "preview"
    },
    showScoreAndSeq: {
      type: Boolean,
      default: true
    },
    questionFavoritesItem: {
      type: Object,
      default() {
        return {};
      }
    },
    itemType: {
      type: String,
      default() {
        return "";
      }
    },
    doingLookAnalysis: {
      type: Boolean,
      default: false
    },
    Collect: {
      type: Boolean,
      default: true
    },
    keys: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  mounted() {
    // 
    if (this.questionFavoritesItem.question_id) {
      this.isCollect = true;
    }
  },
  methods: {
    changeTag() {
      this.isTag = !this.isTag;
      this.$emit("changeTag", this.isTag);
    },
    changeCollect() {
      this.isCollect = !this.isCollect;
      let data = {
        question_id: this.question.id,
        target_type: "assessment"
      };

      this.$emit("changeCollect", data, this.isCollect);
    },
    getAttachmentTypeData(type) {
      const [item] = this.question.attachments.filter(item => {
        return item.module == type;
      });
      return item;
    },
    previewAttachment(fileId) {
      this.dispatch("item-engine", "previewFile", fileId);
      this.dispatch("item-preview", "previewFile", fileId);
    },
    downloadAttachment(fileId) {
      this.dispatch("item-engine", "downloadFile", fileId);
      this.dispatch("item-preview", "downloadFile", fileId);
    },
    lookAnalysis() {
      this.canShowAnalysis = !this.canShowAnalysis;
      this.$emit("setMaterialAnalysis", this.canShowAnalysis, this.keys);
    }
  }
};
</script>
