<template>
  <div>
    <a-row>
      <a-col class="ibs-pr16 ibs-pl16">
        <div>
          <!-- word -->
          <div
            v-if="item.errors && item.errors.stem"
            class="ibs-engine-material ibs-danger-color"
          >
            {{ t("itemEngine.stemErrorMessage") }}
          </div>

          <div class="ibs-engine-material" v-else>
            <div class=" ibs-editor-text" v-html="material"></div>
            <div
              class="ibs-mt16 ibs-engine-material__attachment"
              v-if="getAttachmentTypeData('material')"
            >
              <attachment-preview
                :cdnHost="cdnHost"
                :fileData="getAttachmentTypeData('material')"
                @previewAttachment="previewAttachment"
                @downloadAttachment="downloadAttachment"
                :downloadAttachmentCallback="downloadAttachmentCallback"
                :previewAttachmentCallback="previewAttachmentCallback"
              ></attachment-preview>
            </div>
          </div>
        </div>
      </a-col>
    </a-row>
  </div>
</template>

<script>
import Locale from "@/mixins/locale";
import attachmentPreview from "@/components/attachment-preview/src/attachment.vue";
export default {
  name: "material-title",
  mixins: [Locale],
  inject: [
    "cdnHost",
    "previewAttachmentCallback",
    "downloadAttachmentCallback"
  ],
  components: {
    attachmentPreview
  },
  props: {
    material: {
      type: String,
      default: ""
    },
    item: {
      type: Object,
      default() {
        return {};
      }
    },
    attachments: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  methods: {
    getAttachmentTypeData(type) {
      const [item] = this.attachments.filter(item => {
        return item.module == type;
      });
      return item;
    },
    previewAttachment(fileId) {
      this.$emit("previewAttachment", fileId);
    },
    downloadAttachment(fileId) {
      this.$emit("downloadAttachment", fileId);
    }
  }
};
</script>
