<template>
  <div class="ibs-inspection">
    <div class="ibs-card ibs-card--inspection" v-show="state === 'watching'">
      <div class="ibs-card-head ibs-clearfix ibs-text-left">
        {{ t("inspectionControl.Invigilation_tips") }}
      </div>
      <div class="ibs-card-body ibs-text-center">
        <div class="ibs-inspection-card">
          <!-- <div class="ibs-inspection-card-tips">
            {{  }}
          </div> -->
          <a-alert
            :message="behaviorMap[watchBehavior]"
            :type="watchingState"
          />
          <div class="ibs-inspection-card-title">
            {{ t("inspectionControl.Independent_completion") }}
          </div>
          <div class="ibs-inspection-card-area">
            <div class="ibs-inspection-card-capture">
              <video
                autoplay
                class="ibs-inspection-card-capture-video"
                id="inspection-watching-video"
              ></video>
              <canvas
                class="ibs-inspection-card-capture-canvas"
                id="inspection-watching-canvas"
              ></canvas>
            </div>
          </div>
        </div>
      </div>
    </div>
    <a-modal
      :title="t('inspectionControl.Tips')"
      :visible="visible"
      :closable="closable"
      :maskClosable="false"
      class="ibs-inspection-modal"
      :class="
        `ibs-inspection-${state} ${
          isFaceCaptured ? 'captured' : 'not-captured'
        }`
      "
      :confirmLoading="confirmLoading"
      :destroyOnClose="true"
      :okText="confirmText"
      :getContainer="getContainer"
      width="738px"
      @cancel="handleCancel"
    >
      <div slot="footer">
        <a-button @click="reloadPage" v-show="isFaceCaptured">
          {{ t("inspectionControl.Recapture") }}
        </a-button>
        <a-button
          type="primary"
          @click="handleConfirm"
          :loading="confirmLoading"
          :disabled="!isFaceCaptured && startCapture"
          >{{ confirmText }}</a-button
        >
      </div>
      <div
        v-show="state === 'loading'"
        class="ibs-loading-content ibs-text-center"
      >
        <div>
          <a-icon type="loading" style="font-size: 64px" />
        </div>
        <div class="ibs-loading-text">
          <p>{{ t("inspectionControl.Intelligent_invigilator") }}</p>
        </div>
      </div>
      <div
        v-show="state === 'error'"
        class="ibs-loading-content ibs-text-center"
      >
        <div>
          <a-icon type="info-circle" style="font-size: 64px; color: #E6E6E6" />
        </div>
        <div class="ibs-loading-text">
          <p v-if="mediaError">{{ t("inspectionControl.cameraOff") }}</p>
          <p v-else>{{ t("inspectionControl.Start_failed") }}</p>
        </div>
        <div class="ibs-loading-btn">
          <a-button type="primary" @click="reloadPage">{{
            t("inspectionControl.Retry")
          }}</a-button>
        </div>
      </div>
      <div
        :class="{ actived: state === 'ready' }"
        class="ibs-capture-content ibs-capture-ready"
      >
        <div class="ibs-capture-title">
          {{ t("inspectionControl.Collect__head") }}
        </div>
        <a-row>
          <a-col :span="12">
            <div class="ibs-capture-area" id="ibs-capture"></div>
            <div class="ibs-capture-tips">
              <div class="ibs-capture-text">
                {{ t("inspectionControl.Examination_tips") }}
              </div>
              <div class="ibs-capture-text">
                {{ t("inspectionControl.Collect_pictures") }}
              </div>
            </div>
          </a-col>
          <a-col :span="12">
            <div class="ibs-capture-area">
              <img v-if="captureImgData" :src="captureImgData" alt="" />
            </div>
          </a-col>
        </a-row>
      </div>
    </a-modal>
  </div>
</template>

<script>
import loadScript from "load-script";
import interpret from "./utils/interpret";
import Locale from "@/mixins/locale";
export default {
  name: "inspection-control",
  mixins: [Locale],
  inheritAttrs: false,
  components: {},
  props: {
    mode: {
      type: String,
      default: "watching"
    }
  },
  data() {
    return {
      faceUrl: "",
      stateNum: 0,
      startCapture: false,
      visible: false,
      confirmLoading: false,
      inspectionController: null,
      SDK: null,
      descriptor: null,
      captureImgData: null,
      stateList: ["loading", "ready", "watching", "error"],
      watchBehavior: "normal",
      closable: false,
      behaviorMap: {
        // eslint-disable-next-line no-undef
        no_face: `${this.t("inspectionControl.behaviormap_1")}`,
        // eslint-disable-next-line no-undef
        not_self: `${this.t("inspectionControl.behaviormap_2")}`,
        // eslint-disable-next-line no-undef
        many_face: `${this.t("inspectionControl.behaviormap_3")}`,
        // eslint-disable-next-line no-undef
        page_hide: `${this.t("inspectionControl.behaviormap_4")}`,
        // eslint-disable-next-line no-undef
        normal: `${this.t("inspectionControl.behaviormap_5")}`
      },
      mediaError: false
    };
  },
  computed: {
    watchingState() {
      return this.watchBehavior === "normal" ? "info" : "warning";
    },
    state() {
      return this.stateList[this.stateNum];
    },
    confirmText() {
      return this.captureImgData
        ? // eslint-disable-next-line no-undef
          this.t("inspectionControl.captureImgData_1")
        : // eslint-disable-next-line no-undef
          this.t("inspectionControl.captureImgData_2");
    },
    cancelText() {
      // eslint-disable-next-line no-undef
      return this.captureImgData
        ? this.t("inspectionControl.captureImgData_3")
        : "";
    },
    isFaceCaptured() {
      return !!this.captureImgData;
    }
  },
  watch: {},
  created() {
    this.loadInspectionSDK()
      .then(SDK => {
        this.SDK = SDK;
        // this.inspectionController = new SDK({
        //   id: "ibs-capture"
        // });
        // this.inspectionController.on("capture_real_face.captured", data => {
        //   this.captureImgData = data.capture;
        // });
      })
      .catch(() => {
        this.stateNum = 3;
      });
  },
  mounted() {
    this.$emit("ready");
  },
  methods: {
    interpret,
    reloadPage() {
      window.location.reload();
    },
    captureModal(opts) {
      this.visible = true;
      setTimeout(() => {
        this.captureFace(opts);
      }, 300);
    },
    closeModal() {
      this.visible = false;
      this.confirmLoading = false;
    },
    startWatching() {
      setTimeout(() => {
        this.switchCaptureCore();
        this.inspectionController.start();
      }, 300);
    },
    stopWatching() {
      this.inspectionController.stop();
    },
    switchCaptureCore() {
      const currentVideo = document.getElementById("inspection-watching-video");
      const currentCanvas = document.getElementById(
        "inspection-watching-canvas"
      );
      if (!currentVideo || !currentCanvas) {
        return;
      }
      const changeData = {
        video: currentVideo,
        overlay: currentCanvas,
        width: 192,
        height: 144
      };
      Object.entries(changeData).forEach(([key, value]) => {
        this.inspectionController.faceai[key] = value;
      });
    },
    captureFace(opts) {
      this.faceUrl = opts.faceUrl;
      if (!this.SDK) {
        setTimeout(() => {
          this.captureFace(opts);
        }, 300);
        return;
      }
      this.inspectionController = new this.SDK({
        id: "ibs-capture",
        apiServer: "//inspection-service.qiqiuyun.net"
      });
      this.inspectionController.setToken(opts.token);
      this.initControllerEvents();
      if (!opts.faceUrl) {
        this.stateNum = 1;
        return;
      }
      this.inspectionController.on("initialized", () => {
        this.setRealFaceHandler(opts);
      });
    },
    satrtInseption() {
      this.startWatching();
      this.stateNum = 2;
      this.closeModal();
    },
    initControllerEvents() {
      this.inspectionController.on("inspection.ready", () => {
        if (this.mode === "watching") {
          if (!this.captureImgData || this.faceUrl) {
            this.satrtInseption();
          }
        }
      });
      this.inspectionController.on("capture_real_face.captured", data => {
        console.log("captured", data);
        this.captureImgData = data.capture;
        this.descriptor = data.descriptor;
      });
      this.inspectionController.on("cheat.happened", cheating => {
        let message = this.t("inspectionControl.watchResult");
        message += "status: " + cheating.status + ", ";
        message += "level: " + cheating.level + ", ";
        message += "duration: " + cheating.duration + ", ";
        message += "behavior: " + cheating.behavior + ", ";
        message += ")";
        console.log(message);
        this.watchBehavior = cheating.behavior;
        this.$emit("cheatHappened", cheating);
      });
      this.inspectionController.on("cheat.disappeared", () => {
        this.watchBehavior = "normal";
        this.$emit("cheatDisappeared");
      });

      this.inspectionController.on("pong", pong => {
        this.$emit("cheatPong", pong);
      });

      this.inspectionController.on("error", error => {
        if (error.code === "media_error") {
          this.stateNum = 3;
          this.mediaError = true;
          this.visible = true;
        }
        console.log(error);
      });

      this.inspectionController.on("inspection.started", () => {
        this.$emit("inspectionStarted");
      });

      this.inspectionController.on("inspection.stopped", () => {
        this.$emit("inspectionStopped");
      });
    },
    setRealFaceHandler(opts) {
      console.log(opts);
      this.inspectionController.setRealFace(opts.faceUrl);
    },
    cameraCapture() {
      this.startCapture = true;
      this.inspectionController.captureRealFaces();
    },
    handleConfirm() {
      if (!this.captureImgData) {
        this.cameraCapture();
        return;
      }
      this.confirmLoading = true;
      this.$emit("faceCaptured", {
        capture: this.captureImgData,
        descriptor: this.descriptor
      });
      setTimeout(() => {
        if (this.mode === "watching") {
          this.satrtInseption();
          return;
        }
        this.closeModal();
      }, 500);
    },
    handleCancel() {
      console.log("Clicked cancel button");
      this.visible = false;
    },
    loadInspectionSDK() {
      return new Promise((resolve, reject) => {
        loadScript(
          "//service-cdn-ali.qiqiuyun.net/inspection-sdk/1.0.1/escloud-inspection-sdk.js",
          {
            attrs: {
              id: "escloud-inspection-sdk-script"
            },
            async: true
          },
          err => {
            if (err) {
              reject(err);
            }
            resolve(window.ESCloud && window.ESCloud.Inspection);
          }
        );
      });
    },
    getContainer() {
      return document.getElementById("item-bank-sdk-message");
    }
  }
};
</script>
